.calculator {
  background-color: #000000;
  padding: 70px 10px 10px;
  min-width: 230px;
  min-height: 375px;
  z-index: 1;
}

@media only screen
  and (min-device-width: 375px)
  and (max-device-width: 667px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) {
    .calculator {
      -webkit-animation: scale(1.3) 2s ease;
              animation: scale(1.3) 2s ease;
    }
}

/*@keyframes scale-to-mobile {
  to {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    transform: scale(1.3);
  }
}*/

.hidden {
  opacity: 0;
}

.visible {
  opacity: 1;
  -webkit-transition: opacity 2s ease;
  -o-transition: opacity 2s ease;
  transition: opacity 2s ease;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 50px);
  grid-gap: 10px;
  grid-template-rows: repeat(5, 50px);
  padding-top: 5px;
}

.zero {
  grid-column: 1 / span 2;
  text-align: left;
  padding-left: 18px;
}

.clear, .invert, .percent {
  background-color: #a6a6a6;
}

.clear:active, .invert:active, .percent:active {
  background-color: #ffffff;
  /*transition: background-color 2s;*/
}

.zero, .decimal, .one, .two, .three, .four, .five, .six, .seven, .eight, .nine {
  color: #ffffff;
  background-color: #333333;
}
.zero:active, .decimal:active, .one:active, .two:active, .three:active, .four:active, .five:active, .six:active, .seven:active, .eight:active, .nine:active {
  background-color: #777676;
}

.divide, .multiply, .subtract, .add, .equal {
  color: #ffffff;
  background-color: #ff9500;
}

.clickedOperator {
  color: #ff9500;
  background-color: #ffffff;
  -webkit-transition: all 0.4s cubic-bezier(.92, .15, .84, .51);
  -o-transition: all 0.4s cubic-bezier(.92, .15, .84, .51);
  transition: all 0.4s cubic-bezier(.92, .15, .84, .51);
}

.equal:active {
  background-color: #fccc88;
}

.calculator.visible{
  -webkit-animation: scale 2.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
          animation: scale 2.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@-webkit-keyframes scale {
    from {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes scale {
    from {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



