p {
  position: absolute;
  font: bold 40px helvetica;
  color: #ffffff;
  left: 50px;
  top: 0;
  max-width: 750px;
  padding-right: 50px;
}

.container {
  position: absolute;
  min-width: 100vw;
  min-height: 100vh;
}

.visible {
  background-color: #000000;
}

.hidden {
  opacity: 0;
  background-color: #ffffff;
  -webkit-transition: all 2s;
  -o-transition: all 2s;
  transition: all 2s;
}



